<template>
  <div>
    <div class="row mt-4 mb-4 justify-content-md-center">
      <div class="col-md-8 text-right mb-4">
        <router-link to="products">
          <button type="button" class="btn btn-danger mt-3"></button>
        </router-link>
      </div>
      <div class="col-md-8 text-center mb-4">
        <h1></h1>
      </div>
      <div class="col-md-8">
        <div class="form-row">
          <div class="form-group col-md-12 text-right">
            <div class="form-check">
              <input
                type="checkbox"
                v-model="tempProduct.is_enabled"
                true-value="1"
                false-value="0"
                id="is_enabled"
                class="form-check-input"
              />
              <label for="is_enabled" class="form-check-label"></label>
            </div>
          </div>
          <div class="form-group col-md-12">
            <label for="title"></label>
            <input
              type="text"
              id="title"
              v-model="tempProduct.title"
              placeholder=""
              class="form-control"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="category"></label>
            <select
              class="form-control"
              type="unit"
              id="category"
              v-model="tempProduct.category"
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="price"></label>
            <select
              class="form-control"
              type="unit"
              id="unit"
              v-model="tempProduct.unit"
              placeholder=""
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="origin_price"></label>
            <input
              type="number"
              id="origin_price"
              v-model="tempProduct.origin_price"
              placeholder=""
              class="form-control"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="price"></label>
            <input
              type="number"
              id="price"
              v-model="tempProduct.price"
              placeholder=""
              class="form-control"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="price"></label>
            <input
              type="text"
              id="image"
              v-model="tempProduct.imageUrl"
              placeholder=""
              class="form-control"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputfile"> </label>
            <input class="form-control" ref="file" @change="uploadFile" type="file" id="inputfile" />
          </div>
          <div class="form-group col-md-12 text-center">
            <img :src="tempProduct.imageUrl" />
          </div>
          <div class="col-md-12 text-center">
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tempProduct: {}
    };
  },
  methods: {
    updateProduct() {
      const self = this;
      const api =
        "https://vue-course-api.hexschool.io/api/han_vue/admin/product";
      self.$http.post(api, { data: this.tempProduct }).then(response => {
        //data //{data:this.tempProduct}
        console.log(response.data);
        if (response.data.success) {
          self.$swal("", "", "success");
          self.$router.push("products"); //
        } else {
          self.$swal("", "", "error");
        }
      });
    },
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      const formData = new FormData();
      formData.append("file-to-upload", uploadedFile);
      const api = `https://vue-course-api.hexschool.io/api/han_vue/admin/upload`;
      this.$http
        .post(api, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          console.log(res.data);
        });
    }
  }
};
</script>
<style scoped>
img {
  max-width: 100%;
  max-height: 500px;
}
</style>